* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: #ffffee;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}
.habit-wrap {
  max-width: 600px;
  width: 90%;
  margin: 20px auto ;
  border-radius: 30px;
  overflow: hidden;
  /* border: 5px solid #ffe0b2; */
  background-color: #fbe9e7;
}
.navbar {
  font-size: 2rem;
  padding-top: 1em;
  background-color: #ff8a65;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #fff;
}
.navbar h1 {
  display: inline-block;
  
}
.navbar-logo {
  color: #81c784;
  margin-right: 0.5em;
}
.navbar-title {
  font-weight: bolder;
}
.navbar-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin: 5px auto 10px;
  background-color: #a5d6a7;
  color: white;
  
}
.habits {
  
}
.habits-wrap{
  
}
.add-form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
  background-color: #ff8a65;
}
.add-input {
  font-size: 2rem;
  margin-right: 0.2em;
  max-width: 330px;
  width: 70%;
}

.add-button {
  height: 100%;
  font-size: 1.5rem;
  padding: 0.3em 0.5em;
  background-color: #a5d6a7;
  color: white;
}
.habits-list-wrap li {
  border-bottom: 1px solid #cb9b8c;
;
}

.habit {
  font-size: 2.2rem;
  padding: 0.5em;
  list-style: none;
  display: flex;
  align-items: center;
  position: relative;
  color: #1b1b1b;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: #c5e1a5;
  color: white;
  border-radius: 50%;
}
.button-wrap {
  display: flex;
  align-items: center;
  position: absolute;
  right: 10px;
}
.habit-button {
  font-size: 2.5rem;
  margin: 0 0.2em;
  background-color: transparent;
  
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: #ffb74d;
}

.habit-delete {
  font-size: 2rem;
  color: #ff8a65;
}

.habits-reset {
  display: block;
  font-size: 1.5rem;
  padding: 0.2em 1em;
  border-radius: 5px;
  background-color: salmon;
  color: white;
  margin: 15px auto 15px ;
}

@media screen and (max-width: 800px) {
  .add-input {
    font-size: 1.7rem;
  }
  .add-button {
    font-size: 1.7rem;
    padding: 0.1em 0.3em;
  }
  .habit {
    font-size: 1.5rem;
  }
  .habit-count {
    font-size: 1.5rem;
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
  }
  .habit-button {
    font-size: 2rem;
  }
  .habits-reset {
    font-size: 1.3rem;
  }
}